import dateFormat from 'dateformat';

const dateToString = (date) => {
    const dArray = date.split('-');
    const d = new Date(dArray[0], parseInt(dArray[1])-1, dArray[2]);

    if (d === 'Invalid date'){
        return "";
    }

    return dateFormat(d, "mmmm dS, yyyy");
}

export default dateToString;



